html,
body {
  min-height: 100%; }

img {
  max-width: 100%;
  height: auto; }

.site-header {
  padding-top: 17px;
  padding-bottom: 17px; }

@media (max-width: 991px) {
  .site-header {
    padding: 0; } }

.container {
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  max-width: 1170px;
  padding-left: 20px;
  padding-right: 20px; }

@media (max-width: 1169px) {
