    max-width: 570px; } }

@media (min-width: 569px) {
  .container {
    width: 100%; } }

.button {
  display: inline-block;
  padding: 8px 19px 5px 17px;
  font-size: 16px;
  line-height: 19px;
  text-decoration: none;
  color: #ffffff;
  border: 1px solid #67f3d9; }
  .button:focus {
    outline: none; }

.button--black:hover, .button--black:focus {
  color: #67f3d9;
  border-color: #4b4b4b; }

.menu {
  display: flex;
  flex-wrap: wrap;
  padding-left: 0;
  margin: 0;
  width: 100%;
  max-width: 435px;
  list-style: none; }

.menu__item {
  position: relative;
  padding: 20px; }
  .menu__item:last-child {
    margin-right: 0; }

.menu__link {
  position: relative;
  font-size: 16px;
  line-height: 19px;
  color: #ffffff;
  text-decoration: none; }
  .menu__link[href]:hover {
    color: #67f3d9; }
  .menu__link[href]:focus {
    color: #67f3d9; }

.menu__item--active::after {
  content: " ";
  position: absolute;
  left: 50%;
  bottom: 0;
  width: calc(100% - 40px);
  display: block;
  height: 1px;
  background-color: #67f3d9;
  transform: translateX(-50%); }

@media (max-width: 991px) {
  .menu {
    align-content: center;
    flex-direction: column;
    flex-wrap: nowrap; }
  .menu__link {
    font-size: 24px;
    font-weight: 300; }
  .menu__item {
    padding: 0;
    margin-bottom: 45px; }
    .menu__item:last-child {
      margin-bottom: 0; }
    .menu__item::after {
      display: none; }
  .menu__item--active .menu__link {
    position: relative; }
    .menu__item--active .menu__link::after {
      content: " ";
      position: absolute;
      left: 0;
      bottom: -10px;
      width: 100%;
      height: 1px;
      background-color: #3cc3ae; } }

.logo {
  width: 100%;
  max-width: 65px; }

.logo__link {
  display: block; }

.lang-switch {
  position: relative; }

.lang-switch__list {
  position: absolute;
  top: 100%;
  left: 0;
  padding-left: 0;
  margin: 0;
  list-style: none;
  z-index: -1;
  pointer-events: none;
  opacity: 0; }

.lang-switch__current {
  position: relative;
  padding: 7px 23px 7px 7px;
  font-size: 16px;
  line-height: 19px;
  color: #ffffff;
  background-color: transparent;
  border: none; }
  .lang-switch__current:focus {
    outline: none; }

.lang-switch__item {
  padding-left: 7px;
  padding-right: 7px;
  margin-top: 8px; }
  .lang-switch__item:first-child {
    margin-top: 0; }

.lang-switch__item a {
  font-size: 16px;
  line-height: 19px;
  color: #ffffff;
  text-decoration: none; }
  .lang-switch__item a[href]:hover {
    color: #67f3d9; }
  .lang-switch__item a[href]:focus {
    color: #67f3d9; }

.lang-switch__current-icon {
  position: absolute;
  right: 7px;
  top: 13px; }

.lang-switch__item--active a {
  color: #4b4b4b; }

@media (min-width: 1170px) {
  .lang-switch:hover .lang-switch__list {
    z-index: 1;
    opacity: 1;
    pointer-events: initial; }
  .lang-switch:hover .lang-switch__current {
    color: #67f3d9; }
  .lang-switch:focus-within .lang-switch__current {
    color: #67f3d9; }
  .lang-switch:hover .lang-switch__current-icon {
    transform: rotate(180deg); }
  .lang-switch:focus-within .lang-switch__list {
    z-index: 1;
    opacity: 1;
    pointer-events: initial; }
  .lang-switch:focus-within .lang-switch__current-icon {
    transform: rotate(180deg); } }

@media (max-width: 991px) {
  .lang-switch__list {
    display: flex;
    flex-wrap: wrap;
    opacity: 1;
    position: static;
    z-index: 0;
    pointer-events: initial; }
  .lang-switch__item {
    padding: 0;
    margin-right: 20px;
    margin-top: 0; }
    .lang-switch__item:last-child {
      margin-right: 0; }
  .lang-switch__current {
    display: none; }
  .lang-switch__item a {
    font-size: 24px;
    font-weight: 700; }
  .lang-switch__item--active a {
    color: #3cc3ae; } }

@media (min-width: 992px) and (max-width: 1169px) {
  .lang-switch--open .lang-switch__list {
    z-index: 1;
    opacity: 1;
    pointer-events: initial; }
  .lang-switch--open .lang-switch__current-icon {
    transform: rotate(180deg); } }

.navigation__inner {
  position: relative;
  display: flex;
  align-items: flex-start; }

.navigation__call-back {
