  margin-top: 18px;
  margin-left: 48px; }

.navigation__logo {
  margin-right: 323px; }

.navigation__menu {
  margin-top: 6px; }

.navigation__lang-switch {
  margin-top: 19px;
  margin-left: auto; }

.navigation__toggle {
  display: none; }

